<template>
    <div>
        <div class="city-search">
            <input v-model="keyWord" type="text" class="search-input" placeholder="请输入城市名或拼音">
        </div>
        <div class="search-cent" v-show="keyWord">
            <ul>
                <li v-for="(item,index) in list" :key="index">
                    {{item.name}}
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:"CitySearch",
    data(){
        return {
            keyWord:"",
            list:[],
            timer:null
        }
    },
    props:{
        cities:Object
    },
    watch:{
        keyWord(){

            if(this.timer){
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(() => {
                 var arr = []
           for (let key in this.cities) {
               this.cities[key].forEach(element => {
                   if(element.name.indexOf(this.keyWord) > -1|| element.spell.indexOf(this.keyWord) > -1){
                      console.log(element)
                       arr.push(element)
                   }
               });
             
           }
           this.list = arr;
            }, 1000);
          
           
        }
    }
}
</script>

<style scoped>
    .city-search{
        height: 40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .search-input{
        height: 30px;
        border: 1px solid #eee;
        border-radius: 5px;
        width: 90%;
        text-align: center;
        box-sizing: border-box;
    }
    .search-cent{
        position: absolute;
        background-color: #eee;
        top: 80px;
        left: 0;
        right: 0;
        bottom: 0;
    }
</style>


